<template>
  <div class="hello container">
    <h1 v-html="msg"></h1>
    <div class="two-col-container">

    </div>
    <img src="/static/avatar2.jpg">
    <h2 class="mbot-0" v-html="name"></h2>
    <h3>{{ alias }}</h3>
    <h3><b>{{ jobTitle }}</b> ({{ currentPos }}) </h3>
    <p v-html="message"></p>
  </div>
</template>

<script>
export default {
  name: 'JggAbout',
  data () {
    return {
      msg: 'Who am I?',
      name: 'Jorge Gálvez Gabarda',
      alias: '@jorgalga',
      jobTitle: 'Creative developer',
      currentPos: 'Freelancer',
      message: 'Creative developer and researcher on New Media. Expertise in development of hypermedia projects, digital media integration, audiovisual communication and software engineering. Currently based in Valencia as freelancer.<br><br>If you have any idea to carry on and you need some development feel free to contact me (jorgalga83@gmail.com). <br> <a class="btn" target="_blank" href="https://docs.google.com/forms/d/e/1FAIpQLSfJDn8YDm_Fhzda4x389G9HvnQ4dUmrpxP5fL0__8Cyf-mINA/viewform">Contact</a> <a class="btn" target="_blank" href="https://docs.google.com/document/d/1CFnzFMKuqNPjmQVh1hOPp0VI7ZwMVLFW5-FZ5UweLEY/edit?usp=sharing">CV</a>'
    }
  },
  mounted () {
    console.log('Mounted')
    var res = ''
    for (var i = 0; i < this.msg.length; i++) {
      res += '<span>' + this.msg[i] + '</span>'
    }
    this.msg = res
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hello.container{
    padding-top: 8vh;
    min-height: 100vh;
    position: relative;
    padding-bottom: 10vh;
  }
  .two-col-container{
    width: 100%;
    min-height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
  }
  .divisor{
    background-color: aliceblue;
    width: 100%;
    height: 50px;
  }
  img {
    width: 256px;
    height: auto;
    margin-bottom: 30px;
    /*clip-path: polygon(0 20px, 100% 0, 100% calc(100% - 20px), 0 100%);*/
    filter:grayscale(1) contrast(1.2)
  }
  h1{
    margin-bottom: 8vh;
  }
  p {
    width: 90%;
    max-width: 640px;
    margin: 0 auto;
    margin-bottom: 15px;
  }
  /deep/ h1 span{
    min-width: 50px;
    display: inline-block;
    background-color: white;
    color: black;
    margin: 5px;
  }
  @media only screen and (max-width : 600px) {
    /deep/ h1 span{
      margin: 0;
      margin-bottom: 5px;
    }
  }
</style>



