<template>
  <div id="app">
    <!-- img class="logo" src="./assets/logo2.png" -->
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
/**
FONTS
*/
@font-face {
    font-family: 'made_evolve_sans_evobold';
    src: url('/static/fonts/made_evolve_sans_bold_evo_personal_use-webfont.woff2') format('woff2'),
         url('/static/fonts/made_evolve_sans_bold_evo_personal_use-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;

}
@font-face {
    font-family: 'Gilroy';
    src: url('/static/fonts/Gilroy-Light.woff2') format('woff2'),
        url('/static/fonts/Gilroy-Light.woff') format('woff');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Gilroy';
    src: url('/static/fonts/Gilroy-ExtraBold.woff2') format('woff2'),
        url('/static/fonts/Gilroy-ExtraBold.woff') format('woff');
    font-weight: 800;
    font-style: normal;
    font-display: swap;
}

body, html{
  margin: 0
}
.dg.ac{
  display: none;
}

#app {
  font-family: 'Gilroy';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: aliceblue;
  text-align: center;
  height: 100vh;
  width: 100%;
  position: fixed;
  overflow-y: scroll;
}
#app.no-scroll{
  overflow-y: hidden;
  height: 100vh
}

.logo{
  display:none;
}
h1,h2,h3,p{
  line-height: 1.5;
  margin: 0;
}
h1{
  font-family: 'Gilroy';
  text-transform: uppercase;
  font-weight: 800;
  transform: scale(0.8);
}
h2, h3, p {
  font-family: 'Gilroy';
  margin-bottom: 20px;
  line-height: 1.5;
}
h1{
  font-size: 44px;
  font-weight: 800;
}
h2{
  font-size: 34px;
  font-weight: 800;
  letter-spacing: 1px;
  line-height: 1.5;
}
h3{
  font-size: 26px;
  font-weight: 300;
  letter-spacing: 1px;
}
p{
  font-weight: 100;
  font-size: 20px;
}
@media only screen and (max-width : 600px) {
  h1{
    text-align: left;
  }
}
.mbot-0{
  margin-bottom: 0;
}
/* INJECTED HTML*/
.btn{
    margin: 30px 0 0 0;
    background-color: #07889b;
    padding: 10px 20px;
    display: inline-block;
    text-decoration: none;
    font-weight: 800;
    text-transform: uppercase;
    font-size: 26px;
    letter-spacing: 2px;
    color: #171717;
    color: white;
    cursor: pointer;
  }
.modal700{
    width: 75%;
    display: block;
    margin: 30px auto 0;
    max-width: 700px;
    box-shadow: 0 2px 10px #80808080;
}
.modal500{
    width: 75%;
    display: block;
    margin: 30px auto 0;
    max-width: 500px;
    box-shadow: 0 2px 10px #80808080;
}
.modal-full{
    width: 100%;
    display: block;
    margin: 0 auto;
    margin-bottom: 15px;
}
iframe{
    margin-bottom: 0;
}
iframe.youtube-video{
    width: 100%;
    max-width: 700px;
}
iframe.youtube-video-small{
    width: 100%;
    max-width: 560px;
}

.VueCarousel-dot-container{
  margin-top: -30px !important
}

#selector img{
  width: 60px;
  height: 60px;
  box-shadow: 0 3px 5px rgba(0,0,0,.4);
  margin: 2px;
  cursor:pointer;
  clip-path: polygon(0 5px, 100% 0, 100% calc(100% - 5px), 0 100%);
  transition: 0.25s;
  filter: grayscale(100%);
  opacity: 0.5
}
#selector img:hover{
  filter: grayscale(0);
  transform: translate3d(0,-5px, 0);
  opacity: 1;
}

.webvr-ui-title{
  font-size: 12px
}
a#magic-window{
  color: white !important;
  text-shadow: 0 0 10px black
}

</style>



